import { ArgumentsBoolean } from "~/types/appData";
import { compilePlaceholderFromDataSource as getResult } from "./getDataFromSource";
/**
 * 
 * boolean boolean
 * comparableAverageA & comparableAverageB
 * @param comparableAverageA 1
 * @param comparableAverageB 2
 * @param method 
 * @returns boolean
 */
const getBooleanData = ({
  comparableAverageA,
  comparableAverageB,
  method,
}: ArgumentsBoolean["data"]) => {
  let Left = getResult(comparableAverageA);
  let Right = getResult(comparableAverageB);
  const NLeft = (Number(Left) || Left);
  const NRight = (Number(Right) || Right);
  switch (method) {
    case "===":
      return Left === Right;
    case "!==":
      return Left !== Right;
    case ">=":
      return NLeft >= NRight;
    case "<":
      return NLeft < NRight;
    case ">":
      return NLeft > NRight;
    case "<=":
      return NLeft <= NRight;
    case "==":
      // eslint-disable-next-line 
      return Left == Right;
    case "&&":
      return Left && Right;
    case "||":
      return Left || Right;
    default:
      return false;
  }
};

export default getBooleanData;
