import { useEffect } from "react";

interface Result {
    tag: 'id' | 'updatePage' | 'updateAppData' | 'setIsEditing' | 'updateActivationItem' | 'updateRunningTimes' | 'removeActivationItem' | 'playEventEmit' | 'setCurrentEditorStylePath';
    value: any;
}

/**
 * 
 * @param data {tag, value}
 * @param wind 
 * @param origin 
 */
export const sendMessage = ({tag, value}: Result, wind?: Window | null, origin = window.location.origin) => {
  if (wind) {
    wind.postMessage({tag, value}, origin);
  }
}

/**
 * 
 * @param fn Function
 */
const usePostMessage = (fn: (result: Result) => void) => {
    useEffect(() => {
        const handler = (event: { data: any; }) => {
          if (fn instanceof Function) {
            fn(event.data)
          }
        }
        
        window.addEventListener("message", handler)
        // clean up
        return () => window.removeEventListener("message", handler)
      }, [fn]) // empty array => run only once

    return sendMessage
}


export default usePostMessage;