import { Button, Form, Input, message } from "antd";
import React, { useCallback } from "react";
import { register, registerParams, userResult } from "~/api";

interface Props {
  labelCol?: number;
  wrapperCol?: number;
  onRegister?: (user: userResult) => void;
}

const Login: React.FC<Props> = ({ labelCol, wrapperCol, onRegister }) => {
  const onFinish = useCallback((values: registerParams) => {
    register(values).then((res) => {
        if (onRegister instanceof Function) {
            onRegister(res)
        }
        message.success("")
    }).catch(({ error })=> {
        message.error(error);
    });
  }, [onRegister]);

  const onFinishFailed = (errorInfo: any) => {
    console.log("Failed:", errorInfo);
  };

  return (
    <>
      <h2 style={{ textAlign: "center", marginBottom: "20px" }}></h2>
      <Form
        name="basic"
        labelCol={{ span: labelCol || 8 }}
        wrapperCol={{ span: wrapperCol || 16 }}
        initialValues={{ remember: true }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      >
        <Form.Item
          label=""
          name="username"
          rules={[{ required: true, message: "" }]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          name="password"
          label=""
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
          hasFeedback
        >
          <Input.Password />
        </Form.Item>

        <Form.Item
          name="confirm"
          label=""
          dependencies={["password"]}
          hasFeedback
          rules={[
            {
              required: true,
              message: "",
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue("password") === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error(""));
              },
            }),
          ]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item
          wrapperCol={{ offset: labelCol || 8, span: wrapperCol || 16 }}
        >
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </>
  );
};

export default Login;
