import Modal from './../Modal';
import React from 'react';
import PullToRefresh from 'rmc-pull-updown-to-refresh';
import classNames from 'classnames';
import s from './Records.module.scss';

export interface RecordsProps {
    id?: string;
    className?: string;
    /** */
    visible?: boolean;
    /** */
    onCancel?: () => void;
    /** */
    okText?: string;
    /** */
    title?: string;
    /** */
    disablePullDown?: boolean;
    /**, */
    onPullDown?: () => Promise<any>;
    /** */
    disablePullUp?: boolean;
    /**, */
    onPullUp?: () => Promise<any>;
}

const Records: React.FC<RecordsProps> = ({
    visible,
    onCancel,
    okText,
    title,
    className,
    children,
    onPullDown,
    onPullUp,
    disablePullDown,
    disablePullUp,
    id,
}) => {
    return (
        <Modal
            id={id}
            visible={visible}
            onCancel={onCancel}
            okText={okText || ``}
            onOk={onCancel}
            title={title || ``}
            zIndex={50}
        >
            <div className={classNames(s.recordswrap, className)}>
                <PullToRefresh
                    disablePullDown={disablePullDown}
                    disablePullUp={disablePullUp}
                    onPullUp={onPullUp}
                    onPullDown={onPullDown}
                >
                    {children}
                </PullToRefresh>
            </div>
        </Modal>
    );
};

Records.displayName = 'Records'

export default Records;
