import { SearchOutlined } from "@ant-design/icons";
import { Row, Col, Tooltip, Input, Select, Button } from "antd";
import React, { useCallback, useState } from "react";
import { queryTagParams } from "~/api";

const { Option } = Select;

interface queryParams {
    title?: string,
    terminal?: string,
    tag?: number[]
}

interface Props {
  onClick: (query: queryParams) => void;
  tags: queryTagParams[];
  onChange?: (data: queryParams) => void;
}

const Searchbar: React.FC<Props> = ({onClick, tags, onChange}) => {

  const [query, setQuery] = useState<queryParams>({});

  const onSearch = useCallback(
    (e) => {
      e.preventDefault();
      const params = {};
      Object.keys(query).forEach((key) => {
        if (query[key]?.length) params[key] = key === 'tag' ? query[key]?.join(',') : query[key];
      })
      onClick(params)
    },
    [onClick, query],
  )

  const onChangTag = useCallback(
    (tag) => {
      const data = {...query, tag};
      if (onChange instanceof Function) {
        onChange(data);
      }
      setQuery(data)
    },
    [onChange, query],
  )

  const onChangeTitle = useCallback(
    (e) => {
      const data = {...query, title: e.target.value};
      if (onChange instanceof Function) {
        onChange(data);
      }
      setQuery(data)
    },
    [onChange, query],
  )

  const onChangeType = useCallback(
    (terminal) => {
      const data = {...query, terminal};
      if (onChange instanceof Function) {
        onChange(data);
      }
      setQuery(data)
    },
    [onChange, query],
  )


  return (
    <>
      <Row gutter={[5, 24]}>
        <Col span={4}>
          <Tooltip title="">
            <Input type="text" placeholder="" onChange={onChangeTitle} />
          </Tooltip>
        </Col>
        <Col span={4}>
          <Tooltip title="">
            <Select
              allowClear
              style={{ width: "100%" }}
              placeholder=""
              value={query.terminal}
              onChange={onChangeType}
            >
              <Option value="mobile"></Option>
              <Option value="pc">PC</Option>
            </Select>
          </Tooltip>
        </Col>
        <Col span={8}>
          <Tooltip title="">
            <Select
              mode="multiple"
              allowClear
              style={{ width: "100%" }}
              placeholder=""
              value={query.tag}
              onChange={onChangTag}
            >
              {tags.map(item => (<Option key={item.id} value={`${item.id}`}>{item.name}</Option>))}
            </Select>
          </Tooltip>
        </Col>
        <Col span={2}>
          <Button type="default" icon={<SearchOutlined />} onClick={onSearch}>
            
          </Button>
        </Col>
      </Row>
      <br />
    </>
  );
};

export default Searchbar;
