/* eslint-disable no-underscore-dangle */
import loadScript from './loadScript';

declare global {
  interface Window {
    _hmt: any[];
  }
}
let isTracking = false;
/**
 * 
 */
export const initTrack = (id:string) => {
  if (!id) return;
  window._hmt = window._hmt || [];
  window._hmt.push(['_setAutoPageview', false]);
  loadScript(`https://hm.baidu.com/hm.js?${id}`)?.then(() => {
    // 
    isTracking = true
  });
}

export function trackPageView(...params: any[]) {
  if (!isTracking) {
    console.warn('id ->  -> Id')
    return;
  }
  if (process.env.REACT_APP_PRO !== 'true') {
    if (!params || params.length === 0) {
      console.error('PV/UV: ');
    } else if (params.length > 1) {
      console.warn(`PV/UV: ${params[0]}`);
    } else {
      console.info(`PV/UV: ${params[0]}`);
    }
  }
  window._hmt.push(['_trackPageview', ...params]);
}

export function trackEvent(...params: any[]) {
  if (!isTracking) {
    console.warn('id ->  -> Id')
    return;
  }
  if (process.env.REACT_APP_PRO !== 'true') {
    if (!params) {
      console.error(': ');
    } else if (params.length > 3) {
      console.error(': 3');
    } else {
      console.info(`: ${JSON.stringify(params.slice(0, 3))}`);
    }
  }
  window._hmt.push(['_trackEvent', ...params]);
}
